<template>
	<div>
		<OBJECT ID="OCX" CLASSID="CLSID:876b45b2-5c31-499f-a28b-11eef25c6746" codebase="./../../static/cardrealize.ocx" height="0" width="0"></OBJECT>

		<button @click="test()">test</button>
		<button @click="connect()">connect</button>
	    <button @click="read()">read</button>
	    <!-- <button @click="write()">write</button> -->
	    <!-- <button @click="putKey()">putKey</button> -->
	</div>
</template>

<script>
	import jq from './../../../static/js/jquery-1.8.3.min.js'
	import "babel-polyfill";
	import axios from 'axios';
    export default {
        data() {
            return {}
        },
        mounted:function(){
					// this.connect();
					this.judgeUser();
        },
        computed:{
        	
        },
        methods:{
        	test(){
        		const url = 'api/issuing-card/hello';
				axios.get(url, {}).then(response => {
					console.log(response)
				})
        	},
        	connect(){
				var data = OCX.OCX_Connect();
				alert(data);
			  },
				
			read(){				
				var data = OCX.OCX_Read3();
				alert(data);
			  },
				
			write1(){
				$.getJSON('${ctx}/writeCard', function(result) {
			      if(result.success) {
			        var data = OCX.OCX_Write(result.data);
			        alert(data);
			      } else {
					alert("Sorry.");
				  }
			    });
			  },
				
			putKey(){
			  	$.getJSON('${ctx}/putKey', function(result) {
			      if(result.success) {
			        var data = OCX.OCX_PutKey(result.data.first, result.data.second);
			        alert(data);
			      } else {
					alert("Sorry.");
				  }
			    });  
			  }
        }

    }
</script>

<style scoped>
    

</style>



